import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

import textField from 'muse-ui/textField';

import Utils from '~/utils';
import { ToastMsg } from '~/entity/toast';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';

import Api from '~/api';
import { IVerificationConfig, CACHE_NAME } from '~/cache';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class Comp extends Vue {

    @Action('showToast')
    showToast;

    @Action('getBindInfo')
    getBindInfo;

    edit = false;

    aliPaymentId: string = null;

    aliPaymentName: string = null;

    buttonTxt: string = null;

    BUTTON_TXT = {
        CHANGE: '修改绑定',
        BIND: '绑定'
    };

    get buttonEnable() {
        if (this.buttonTxt === this.BUTTON_TXT.CHANGE) {
            return true;
        } else if (this.buttonTxt === this.BUTTON_TXT.BIND) {
            if (this.aliPaymentId.length > 0 && this.aliPaymentName.length > 0) {
                return true;
            }
        }
        return false;
    }

    activated() {
       this.aliPaymentId = this['$store'].state.user.aliPaymentId || '暂未绑定';
       this.aliPaymentName = this['$store'].state.user.aliPaymentName || '暂未绑定';
       this.buttonTxt = this.BUTTON_TXT.CHANGE;
       this.edit = false;
    }
    
    clickButton() {
        if (this.buttonTxt === this.BUTTON_TXT.CHANGE) {
            this.edit = true;
            this.buttonTxt = this.BUTTON_TXT.BIND;
        } else if (this.buttonTxt === this.BUTTON_TXT.BIND) {
            const aliPaymentId = this.aliPaymentId;
            const aliPaymentName = this.aliPaymentName;

            if (!Utils.checkEmail(aliPaymentId) && !Utils.checkPhoneNumber(aliPaymentId)) {
                this.showToast(new ToastMsg('账号必须是邮箱或者手机号'));
                return;
            }

            const subject = new Subject<string>();
            const subscription = subject.subscribe(async (code) => {
                try {
                    const res = await Api.changBindPayment(1, aliPaymentName, aliPaymentId, code);
                    
                    // 更换成功,重新获取绑定信息
                    await this.getBindInfo();
                    this.showToast(new ToastMsg('更换支付宝账号成功'));

                    // 页面回退
                    this['$router'].go(-1);
                } catch (e) {
                    this.showToast(new ToastMsg(e.message));
                }
            });

            const config: IVerificationConfig = {
                phone: this['$store'].state.user.user.tel,
                type: 'bindThirdPayment',
                subject,
                subscription
            };

            Utils.saveItem(CACHE_NAME.verificationConfig, config);

            this['$router'].push({
                name: 'verificationCode'
            });
        }
    }

    editHint(name: string) {
        if (!this.edit) {
            this.showToast(new ToastMsg('请先点击下方修改绑定按钮再进行编辑'));
        }
    }
}



